body{
  margin: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: 'Fjalla One', sans-serif;
  color: white;
}
@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap');
.Container{
  width: 400px;
  height: 500px;
  background-color: rgb(119, 119, 119);
}
.Container button{
  font-family: 'Fjalla One', sans-serif;
  width: calc(100% / 4);
  height: calc((100% - 100px) / 5);
  background-color: rgb(71, 70, 70);
  border: .5px black solid;
  color: white;
  font-size: 2.5rem;
  cursor: pointer;
}
.Header{
  width: calc(100% - 42px);
  padding: 0px;
  margin: 0px;
  display: flex;
  align-items: center;
  height: 100px;
  padding-inline: 20px;
  border: .5px black solid;
  background-color: rgb(125, 150, 125);
  overflow-x: auto;
}